html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  background-image: url("../img//Bckgr.png");
  background-size: cover;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
  max-width: 1440px;
  margin: 0 auto;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

header {
  border-bottom: 2px solid #e5e5e5;
}

.container {
  padding: 0;
  min-width: 1440px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 29px;
}
.container .logo {
  align-self: flex-start;
  margin-right: 110px;
}
.container .menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.container .menu ul li {
  text-align: justify;
  padding: 10px 18px 50px 18px;
  font-size: 16px;
  line-height: 19px;
}
.container .menu ul li a {
  color: black;
  position: relative;
}
.container .menu ul li a:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -53px;
  width: 100%;
  height: 5px;
  background-color: transparent;
  transition: background-color 0.3s ease;
  transition: all 0.3s ease-in-out;
}
.container .menu ul li a:hover::after {
  background-color: #6AA0FF;
}
.container .auth {
  font-size: 16px;
  line-height: 19px;
  display: flex;
  align-self: flex-start;
  margin-left: 20px;
}
.container .auth button {
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  background-color: transparent;
  padding: 10px 27px;
  border: 1px solid black;
  border-radius: 20px;
}
.container .auth button:first-child {
  margin-right: 10px;
}
.container .auth button:hover {
  background-color: #6AA0FF;
  color: white;
  border-color: #6AA0FF;
}

.first {
  position: relative;
  background-image: url("../../img/svg/1.svg");
  background-position-x: calc(100% + 60px);
  background-position-y: -60px;
  background-repeat: no-repeat;
  background-size: 70%;
  min-width: 1440px;
  height: 650px;
  border-bottom: 2px solid #e5e5e5;
}
.first .left {
  position: absolute;
  margin-top: 90px;
  margin-left: 120px;
  width: 500px;
}
.first h3 {
  margin: 0;
  font-family: "Montserrat";
  font-weight: 600;
  font-size: 52px;
  text-transform: uppercase;
}
.first .subtext {
  font-weight: 400;
  line-height: 30px;
  font-size: 20px;
  margin: 45px 0 60px 0;
}
.first button {
  cursor: pointer;
  padding: 17px 28px;
  border: none;
  border-radius: 25px;
  background-color: #6AA0FF;
  color: white;
}

.second {
  background-image: url("../../img/svg//2.svg");
  background-position-x: left;
  background-position-y: -60px;
  background-repeat: no-repeat;
  background-size: 60%;
  min-width: 1440px;
  height: 900px;
  position: relative;
  border-bottom: 2px solid #e5e5e5;
}
.second::after {
  content: "";
  background-image: url("../../img/svg/lockupper.svg");
  height: 145px;
  width: 145px;
  display: block;
  position: absolute;
  bottom: -85px;
  left: 50%;
  transform: translateX(-50%);
}
.second h3 {
  margin-top: 90px;
  margin-bottom: 90px;
  text-transform: uppercase;
  text-align: center;
  font-size: 32px;
  font-family: "Montserrat";
  font-weight: 600;
}
.second h3 span {
  font-weight: 300;
}
.second .right {
  position: absolute;
  right: 20px;
  display: flex;
  justify-content: end;
  flex-wrap: wrap;
  width: 720px;
  gap: 15px;
}
.second .right .item {
  height: 300px;
  width: 228px;
  padding: 40px 24px;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
}
.second .right .item h5 {
  font-family: "Montserrat";
  margin: 30px 0 20px 0;
  font-size: 20px;
  font-weight: 600;
  width: 130px;
}
.second .right .item .subtext {
  font-weight: 300;
}

.third {
  position: relative;
  height: 550px;
  min-width: 1440px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 2px solid #e5e5e5;
}
.third::before {
  content: "";
  background-image: url("../../img/svg/shield.svg");
  height: 250px;
  width: 220px;
  display: block;
  position: absolute;
  left: -25px;
  top: 20px;
}
.third::after {
  content: "";
  background-image: url("../../img/svg/lock.svg");
  height: 270px;
  width: 260px;
  display: block;
  position: absolute;
  right: 0;
  bottom: 40px;
}
.third h3 {
  margin-top: 110px;
  margin-bottom: 45px;
  text-transform: uppercase;
  text-align: center;
  font-size: 32px;
  font-family: "Montserrat";
  font-weight: 600;
}
.third h3 span {
  font-weight: 300;
}
.third .subtext {
  padding: 0 10px;
  font-size: 18px;
  line-height: 30px;
  text-align: center;
  width: 63%;
}
.third button {
  cursor: pointer;
  font-size: 18px;
  padding: 17px 19px;
  border: none;
  margin-top: 77px;
  border-radius: 25px;
  background-color: #6AA0FF;
  color: white;
}

.fourth {
  height: 700px;
  min-width: 1440px;
  background-image: url("../../img/svg/4.svg");
  background-repeat: no-repeat;
  background-position-x: calc(100% - 50px);
  background-position-y: 60px;
  border-bottom: 2px solid #e5e5e5;
}
.fourth .wrapp {
  margin-left: 120px;
}
.fourth .wrapp h3 {
  margin-top: 110px;
  margin-bottom: 60px;
  text-transform: uppercase;
  text-align: center;
  font-size: 32px;
  font-family: "Montserrat";
  font-weight: 600;
}
.fourth .wrapp h3 span {
  font-weight: 300;
}
.fourth .wrapp .left ul {
  width: 417px;
  list-style: none;
}
.fourth .wrapp .left ul li {
  position: relative;
}
.fourth .wrapp .left ul li::before {
  content: "";
  background-image: url("../../img/svg/ok.svg");
  height: 30px;
  width: 30px;
  display: block;
  position: absolute;
  left: -50px;
}
.fourth .wrapp .left ul li h5 {
  font-family: "Montserrat";
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 15px;
  text-transform: capitalize;
}
.fourth .wrapp .left ul li .subtext {
  font-weight: 100;
  margin-bottom: 45px;
}

.fifth {
  height: 500px;
  background-image: url("../../img/svg/5.svg");
  background-position-x: -250px;
  background-position-y: 20px;
  background-repeat: no-repeat;
  background-size: 60%;
  min-width: 1440px;
  position: relative;
}
.fifth .right {
  position: absolute;
  top: 90px;
  right: 100px;
  width: 50%;
  display: flex;
  flex-direction: column;
}
.fifth .right h3 {
  margin-top: 0;
  padding: 0 100px;
  margin-bottom: 45px;
  text-transform: uppercase;
  text-align: center;
  font-size: 32px;
  font-family: "Montserrat";
  font-weight: 600;
}
.fifth .right h3 span {
  font-weight: 300;
}
.fifth .right .subtext {
  text-align: center;
  padding: 0 40px;
  font-size: 18px;
  line-height: 30px;
  margin-bottom: 60px;
}
.fifth .right button {
  margin: 0 auto;
  cursor: pointer;
  font-size: 18px;
  padding: 17px 19px;
  border: none;
  border-radius: 25px;
  background-color: #6AA0FF;
  color: white;
}

footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #01141A;
  height: 200px;
}
footer .buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
  width: 145px;
  margin-left: 120px;
}
footer .buttons button {
  cursor: pointer;
  background-color: #FAFBFD;
  width: 145px;
  height: 40px;
  border: none;
  border-radius: 20px;
}
footer .list {
  display: grid;
  grid-template-columns: repeat(3, 250px);
  align-self: center;
  height: 100px;
  margin-left: 200px;
}
footer .list .item {
  color: white;
  margin-bottom: 15px;
}
footer .social {
  display: flex;
  gap: 20px;
  align-self: center;
  margin-bottom: 60px;
  margin-right: 145px;
}